var LatestTweet = React.createClass({

	getInitialState: function() {
		return {
			tweet: [],
			user: []
		};
	},

	componentDidMount: function() {
		$.ajax({
			url: 'http://api-tomo.pagu.co/twitter',
			dataType: 'json',
			cache: true,
			success: function(data) {
				var tweet = data.tweets[0];
				var user = tweet.user.screen_name;
				this.setState({
					tweet: tweet,
					user: user
				});
			}.bind(this),
			error: function(xhr, status, err) {
				console.error(this.props.url, status, err.toString());
			}.bind(this)
		});
	},

	render: function() {
		return (
			<div className="lastTweeted">
				<p>I last tweeted...</p>
				<p className="tweet">{ this.state.tweet.text } <small><a href={ 'https://twitter.com/' + this.state.user + '/status/' + this.state.tweet.id_str }>{ this.state.tweet.created_at }</a></small></p>
			</div>
		);
	}

});

var LatestInstagram = React.createClass({

	getInitialState: function() {
		return {
			link: '',
			image: [],
			caption: ''
		};
	},

	componentDidMount: function() {
		$.ajax({
			url: 'http://api-tomo.pagu.co/instagram',
			dataType: 'json',
			cache: true,
			success: function(data) {
				var lastPhoto = data.photos[0];
				var image = lastPhoto.images.thumbnail;
				var caption = lastPhoto.caption.text;

				this.setState({
					link: lastPhoto.link,
					image: image,
					caption: caption
				});
			}.bind(this),
			error: function(xhr, status, err) {
				console.error(this.props.url, status, err.toString());
			}.bind(this)
		});
	},

	render: function() {
		return (
			<div className="lastInstagrammed">
				<p>I last instagramed...</p>
				<a href={ this.state.link } title={ this.state.caption } className="instagram">
					<img src={ this.state.image.url } height={ this.state.image.height } width={ this.state.image.width } alt={ this.state.caption } />
				</a>
			</div>
		);
	}

});

var Intro = React.createClass({

	render: function() {
		return (
			<div>
				<h1>Hi, I'm Thomas Moore</h1>
				<LatestTweet />
				<LatestInstagram />
			</div>
		)
	}

});

React.render(
	<Intro />,
	document.getElementById('intro')
);
